import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { increment } from "../../Redux/UpdateShowGames";
import { getGames } from "../../Redux/gamesSlice";
import Game from "../Game/Game";
import Loading from "../Loading/Loading";

function AllGames() {
  const { games, isLoading } = useSelector((store) => store.games);
  const { start, end } = useSelector((store) => store.update);

  const disPatch = useDispatch();

  // Function to handel games From Redux game File
  function disPlayGames() {
    disPatch(getGames());
  }

  useEffect(() => {
    disPlayGames();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <section className="allGames">
      <div className="container">
        <div className="heading d-flex align-items-center justify-content-between">

          <h2>Games</h2>
          {/* Btn display next and previous Games */}
          <ul className="wrapper">
            <li className="icon black">
              <span className="tooltip">Back</span>
              <span><svg onClick={() =>
                disPatch(
                  increment({
                    type: "increment",
                    payload: {
                      parameter1: "decrease",
                      parameter2: games,
                    },
                  })
                )
              } viewBox="0 0 16 16" className="bi bi-chevron-double-left" fill="currentColor" height="16" width="16" xmlns="http://www.w3.org/2000/svg">
                <path d="M8.354 1.646a.5.5 0 0 1 0 .708L2.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z" fillRule="evenodd"></path>
                <path d="M12.354 1.646a.5.5 0 0 1 0 .708L6.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z" fillRule="evenodd"></path>
              </svg></span>
            </li>
            <li className="icon black">
              <span className="tooltip"  >More</span>
              <span><svg onClick={() =>
                disPatch(
                  increment({
                    type: "increment",
                    payload: {
                      nameFunction: "increase",
                      game: games.length,
                    },
                  })
                )
              } viewBox="0 0 16 16" className="bi bi-chevron-double-right" fill="currentColor" height="16" width="16" xmlns="http://www.w3.org/2000/svg">
                <path d="M3.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L9.293 8 3.646 2.354a.5.5 0 0 1 0-.708z" fillRule="evenodd"></path>
                <path d="M7.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L13.293 8 7.646 2.354a.5.5 0 0 1 0-.708z" fillRule="evenodd"></path>
              </svg></span>
            </li>
          </ul>
        </div>


        {isLoading ? <Loading /> : ""}
        <div className="row g-5">
          {/* Dispaly Games */}
          {games?.slice(start, end).map((item, index) => (
            <Game
              numberOfgame={start + index + 1}
              key={item.id}
              id={item.id}
              title={item.title}
              img={item.thumbnail}
              url={item.game_url}
              date={item.release_date}
            />
          ))}
        </div>
      </div>
    </section >
  );
}

export default AllGames;
