import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getCategoryGame } from "../../Redux/gameCategory";
import Game from "../Game/Game";
import Loading from "../Loading/Loading";
import { Helmet } from "react-helmet";

function Category() {
  const { start, end } = useSelector((store) => store.update);

  const { categoreis, isLoading } = useSelector((store) => store.categoryGame);
  const [loadingCategory, setLoadingCategory] = useState(false);

  let disPatch = useDispatch();

  function getCategory(category) {
    try {
      setLoadingCategory(true);
      disPatch(getCategoryGame(category));
      setLoadingCategory(false);
    } catch (error) {
      setLoadingCategory(false);
      console.error("Error fetching category:", error);
    }
  }

  useEffect(() => {
    getCategory();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Categories</title>
        <link rel="canonical" href="http://mysite.com/example" />
      </Helmet>
      <section className="category">
        <div className="container">

          <div className="heading d-flex align-items-center justify-content-between">

            <h2>Categories</h2>

            <div className="paste-button">
              <button className="button">More &nbsp; ▼</button>
              <div className="dropdown-content">

                <button id="top" className="btn " onClick={() => getCategory("mmorpg")}>
                  Mmorpg
                </button>

                <button id="middle" className="btn " onClick={() => getCategory("shooter")}>
                  Shooter
                </button>

                <button id="bottom" className="btn " onClick={() => getCategory("sailing")}>
                  Sailing
                </button>

                <button id="last" className="btn " onClick={() => getCategory("pixel")}>
                  Pixel
                </button>
              </div>
            </div>


          </div>

          {isLoading || loadingCategory ? (
            <Loading />
          ) : (
            <>
              <div className="row g-5">
                {/* Dispaly Games */}

                {categoreis?.slice(start, end).map((item, index) => (
                  <Game
                    numberOfgame={start + index + 1}
                    key={item.id}
                    id={item.id}
                    title={item.title}
                    img={item.thumbnail}
                    url={item.game_url}
                    date={item.release_date}
                  />
                ))}
              </div>
            </>
          )}
        </div>
      </section>
    </>
  );
}

export default Category;
